:root {
    --primary-color: #5b24ff;
    --primary-color-alpha: rgba(67, 31, 255, 0.1);
    --primary-color-red: #ff4545;
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
}

#root {
    height: 100%;
}

.auth__form-container {
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: url("./assets/singUp.jpg");
}

.auth__form-container_fields {
    /*flex: 2;*/
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 2rem;
    background: #005fff;
}

.auth__form-container_image {
    flex: 3;
    display: flex;
    box-shadow: 1px 0 5px rgba(0, 0, 0, 0.05);
}

.auth__form-container_image img {
    width: 100%;
    height: 100%;
}

.auth__form-container_fields-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    min-width: 330px;
    padding: 2rem;
    box-shadow: 0 1px 5px rgb(0 0 0 / 30%);
    border-radius: 5px;
    transition: 0.8s ease;
    background: transparent;
}

.auth__form-container_fields-content p {
    font-size: 1.5rem;
    font-family: Arial, Helvetica, sans-serif;
    color: #05245a;
    font-weight: 900;
    margin-top: 0;
}

.auth__form-container_fields-content_input {
    display: flex;
    flex-direction: column;
    position: relative;

    margin: 1rem 0;
}

.auth__form-container_fields-content_input label {
    margin-bottom: 0.45rem;
    color: rgb(61, 79, 88);
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 1px;
    line-height: 1.3;
}

.auth__form-container_fields-content_input input {
    padding: 0.55rem 0.4rem;
    border: 1px solid rgb(184, 196, 194);
    border-radius: 4px;
    font-size: 14px;
    outline: none;
    transition: all 150ms ease-in-out 0s;
    width: 85%;
    background: transparent;
    position: relative;
}

.auth__form-container_fields-content_input input::placeholder {
    color: #adadad;
    width: 100%;
    font-weight: unset;
    font-family: Arial, Helvetica, sans-serif;
}

.auth__form-container_fields-content_input input:hover {
    border-color: #dcdddd;
}

.auth__form-container_fields-content_input input:focus,
.auth__form-container_fields-content_input input:active {
    box-shadow: 0 0 0 2px var(--primary-color);
    border-color: var(--primary-color);
}

.auth__form-container_fields-content_input-password {
    position: absolute;
    right: 13%;
    top: 50%;
    cursor: pointer;
}

.auth__form-container_fields-content_button {
    margin-top: 2rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: flex-start;
}

.auth__form-container_fields-content_button button {
    border-radius: 4px;
    background: transparent;
    border: none;
    box-shadow: 0 1px 5px rgb(0 0 0 / 30%);
    color: #adadad;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    padding: 0.7rem 1.2rem;
    outline: none;
    cursor: pointer;
    transition: 0.3s ease;
}

.auth__form-container_fields-content_button button:disabled {
    background: transparent;
    opacity: 80%;
    cursor: default;
    transition: none;
}

.auth__form-container_fields-content_button button:hover {
    background: var(--primary-color);
    color: #fff;
}

.auth__form-container_fields-content_button button:disabled:hover {
    background: #b1b1b1;
}

.auth__form-container_fields-account {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-top: 0.2rem;
}

.auth__form-container_fields-account p {
    font-size: 14px;
    color: #000;
    font-weight: 500;
}

.auth__form-container_fields-account span {
    color: #05245a;
    cursor: pointer;
    font-weight: 700;
}

@media screen and (max-width: 800px) {
    .auth__form-container {
        flex-direction: column-reverse;
    }

    .auth__form-container_fields {
        justify-content: flex-start;
    }

    .auth__form-container_image {
        height: 100px;
        flex: none;
        box-shadow: none;
    }

    .auth__form-container_image img {
        object-fit: cover;
    }
}

@media screen and (max-width: 375px) {
    .auth__form-container_fields {
        padding: 2rem 0.5rem;
    }

    .auth__form-container_fields-content_input input {
        width: 95%;
    }

    .auth__form-container_fields-content_input-password {
        right: 3%;
    }
}

.app__wrapper {
    display: flex;
    flex: 1;
    /* height: 800px; */
    height: 100%;
    /* border-radius: 16px; */
    box-shadow: rgba(0, 0, 0, 0.33) 0 1px 4px 0;
}

.str-chat-channel-list {
    height: fit-content;
    margin-bottom: 8px;
}

.str-chat-channel {
    min-height: 100%;
}

.str-chat__load-more-button {
    display: none;
}

.str-chat__input-footer {
    display: none;
}

.str-chat__date-separator {
    margin: 16px 24px;
}

.str-chat__message-notification {
    background: var(--primary-color);
    cursor: pointer;
}

@media screen and (max-width: 960px) {
    .str-chat-channel-list.team.light {
        position: unset;
        left: unset;
        top: unset;
        z-index: unset;
        min-height: unset;
        overflow-y: unset;
        box-shadow: unset;
        transition: unset;
    }
}

.channel-empty__container {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 20px;
    margin-right: 20px;
    padding-bottom: 20px;
}

.channel-empty__avatars {
    display: flex;
}

.channel-empty__avatars div:first-child {
    z-index: 3;
}

.channel-empty__avatars div:nth-child(2) {
    position: relative;
    right: 32px;
    z-index: 2;
}

.channel-empty__avatars div:nth-child(3) {
    position: relative;
    right: 64px;
    z-index: 1;
}

.channel-empty__avatars .str-chat__avatar {
    margin-right: 0;
}

.channel-empty__first {
    font-family: Helvetica Neue, sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 120%;
    color: #2c2c30;
    margin-bottom: 10px;
}

