}

.custom-thread-header {
    height: 62px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}

.custom-thread-header__left {
    display: flex;
    align-items: center;
