.channel-name-input__wrapper input:focus {
    border: 1px solid var(--primary-color);
    outline: none;
}

.channel-name-input__wrapper input::placeholder {
    font-weight: 300;
    width: 100%;
    color: rgba(0, 0, 0, 0.5);
}


.error {
    position: relative;
}

.error input,
.error input:hover,
.error input:focus {
    border: 1px solid var(--primary-color-red);
}

.error input::placeholder {
    color: var(--primary-color-red);
}

.error::after {
    position: absolute;
    content: attr(data);
    right: 10%;
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
    color: var(--primary-color-red);
}

.channel-name-input__wrapper.error::after {
    position: absolute;
    content: attr(data);
    left: 20px;
    bottom: 10px;
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
    color: var(--primary-color-red);
}

