}

.team-channel-list {
    background: var(--primary-color);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.team-channel-list__message {
    color: #ffffff;
    padding: 0 16px;
}

.team-channel-list__message.loading {
    height: 115px;
}

.team-channel-list__header {
    padding: 0 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.team-channel-list__header svg {
    cursor: pointer;
}

.team-channel-list__header__title {
    font-family: Helvetica Neue, sans-serif;
    font-size: 13px;
    line-height: 16px;
    height: 16px;
    color: rgba(255, 255, 255, 0.66);
    margin-bottom: 10px;
}

.channel-preview__wrapper {
    height: 37px;
    display: flex;
    align-items: center;
}

.channel-preview__wrapper__selected {
    height: auto;
    display: flex;
    align-items: center;
    background: rgba(0, 0, 0, 0.2);
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    font-weight: bold;
    margin-right: 16px;
    cursor: pointer;
    z-index: 2;
}

.channel-preview__wrapper:hover {
    background: rgba(0, 0, 0, 0.2);
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    font-weight: bold;
    margin-right: 16px;
    cursor: pointer;
}

.channel-preview__item {
    display: flex;
    align-items: center;
    font-family: Helvetica Neue, sans-serif;
    font-size: 14px;
    color: #ffffff;
    padding: 0 20px;
    height: 100%;
    width: 100%;
    text-overflow: ellipsis;
    word-break: break-all;
}

.channel-preview__item.multi {
    max-width: 220px;
}

.channel-preview__item.single .str-chat__avatar {
    margin-right: 12px;
}

.channel-preview__item.multi .str-chat__avatar {
    margin-right: 0;
}

.channel-preview__item.multi span:first-child {
    position: relative;
    z-index: 2;
    bottom: 6px;
}

.channel-preview__item.multi div:nth-child(2) {
    position: relative;
    right: 10px;
    z-index: 1;
    margin-bottom: 0;
}

.channel-preview__item.multi p {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.str-chat__message-team:hover {
    background: #e9e9ea;
}

.str-chat__message-team-group {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.str-chat__message-team-content p {
    font-family: Helvetica Neue, sans-serif;
    font-style: normal;
    font-weight: normal;
    line-height: 120%;
    color: #000000;
    margin: 0;
    background: none;
}

.str-chat__message-team:hover .str-chat__message-team-content {
    background: none;
}

.str-chat__message-team-meta {
    justify-content: flex-start;
    align-items: center;
}

.str-chat__message-team--bottom time {
    display: none;
}

.str-chat__message-team--middle time {
    display: none;
}

.str-chat__message-team-meta time {
    visibility: visible;
    z-index: 2;
    font-family: Helvetica Neue, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    color: #858688;
    margin-top: 4px;
}

.str-chat__message-team-author strong {
    font-family: Helvetica Neue, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 11px;
    line-height: 120%;
    color: #858688;
    margin: 0 8px 0 0;
}

.str-chat__message-team-content--text {
    border-left: none;
}

.str-chat__message-team--received {
    padding-left: 20px;
}

.str-chat__message-replies-count-button {
    z-index: 2;
    display: flex;
    /* justify-content: flex-end; */
