.team-channel-header_menu-icon {
    display: none;
}

.channel-list__container-toggle {
    display: none;
    height: 50px;
    width: 50px;
    background: #005fff;
    position: absolute;
    right: -2%;
    top: 50%;
    border-radius: 50%;
    z-index: 2;
}

.channel-list__container-responsive {
    display: none;
    height: 100%;
    box-shadow: inset 1px 0 0 rgba(0, 0, 0, 0.1);
    position: absolute;
    width: 90%;
    top: 0;
    z-index: 5;
    transition: 0.8s ease;
}

.str-chat__input-flat {
    width: 100%;
    padding: 10px;
}

@media screen and (max-width: 960px) {
    .channel-list__container {
        display: none;
    }

    .team-channel-header_menu-icon {
        display: flex;
    }

    .team-channel-header__container {
        height: 70px;
    }

    .team-channel-header__name-wrapper {
        height: 100%;
        max-width: unset;
    }

    .team-channel-header__name-multi {
        margin-right: 8px;
        align-items: center;
        flex-direction: column;
    }

    .team-channel-header__name.user {
        font-size: 10px;
        margin: 5px;
        text-align: center;
    }

    .team-channel-header__right {
        display: flex;
        padding-left: 10px;
    }

    .channel-list__container-responsive {
        display: flex;
    }

    .channel-list__list__wrapper {
        width: 100%;
    }

    .channel-list__container-toggle {
        display: flex;
    }

    .channel-search__input__wrapper {
        width: 90%;
        padding-left: 10px;
        justify-content: flex-start;
    }

    .channel-search__input__text {
        width: inherit;
    }

    .channel-preview__item.multi {
        width: 80%;
    }

    .channel-search__results {
        top: 100%;
        left: 5%;
    }
}

@media screen and (max-width: 650px) {
    .channel-list__container-toggle {
        right: -3%;
    }
}

@media screen and (max-width: 400px) {
    .channel-preview__item.multi {
        width: 150px;
    }

    .channel-list__container-toggle {
        right: -5%;
    }
}

@media screen and (max-width: 320px) {
    .channel-list__sidebar {
        display: none;
    }
}